.makes__container {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1025px) {
  .makes__container {
    padding-block-start: 271px;
  }
}
@media (max-width: 992.99px) {
  .makes__container {
    padding-block-start: 114px;
  }
}

.makes__body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-block-end: 48px;
}

.makes__body-title {
  padding-block-end: 16px;
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: 0px;
  max-width: 650px;
}
@media (max-width: 992.99px) {
  .makes__body-title {
    font-size: 32px;
    line-height: 40px;
  }
}

.makes__body-text {
  max-width: 650px;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}